<template>
  <div class="wrapper">
    <h1>Поиск фильмов с использованием API The Open Movie Database</h1>
    <div class="search-form">
      <div class="searchTitle items-wrapper">
        <label for="searchTitle">Название</label>
        <input type="text" name="searchTitle" v-model="searchTitle">
      </div>
      <div class="searchYear items-wrapper">
        <label for="searchYear">Год выпуска</label>
        <input type="text" name="searchYear" v-model="searchYear" pattern="\d+?">
      </div>
      <div class="searchYear items-wrapper">
        <button @click="findFilms"
          >Найти
        </button>
      </div>
    </div>

    <div id="result" class="filmInfo" v-bind:class="{ fullInfo: isFullInfo }" v-if="isFound">
      <div class="poster">
        <img v-bind:src="img">
      </div>
      <div class="information">
          <div class="row">
            <h2>Информация о фильме</h2>
            <button
                @click="showMore"
                >
                {{ btnTitle }}
              </button>
          </div>
          <div class="row">
            <div class="information-text" v-if="!isFullInfo">
              <div class="row">
                <div class="col">
                  <p class="name">
                    Title
                  </p>
                </div>
                <div class="col">
                  <p>{{ filmInfo.Title.value }}</p>
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <p class="name">
                    Year
                  </p>
                </div>
                <div class="col">
                  <p>{{ filmInfo.Year.value }}</p>
                </div>
              </div>
              <div class="row">
                <div class="col">
                  <p class="name">
                    Genre
                  </p>
                </div>
                <div class="col">
                  <p>{{ filmInfo.Genre.value }}</p>
                </div>
              </div>
            </div>
            <div class="information-text" v-else>
              <div class="row" v-for="item in filmInfo" v-bind:key="item.name">
                  <div class="col">
                    <p class="name">
                      {{item.name.ru}}
                    </p>
                  </div>
                  <div class="col" v-if="item.name.en!='Ratings'">
                    <p>{{item.value}}</p>
                  </div>
                  <div class="col" v-else>
                    <div class="rating" v-for="raiting in item.value" v-bind:key="raiting.Source">
                        <div class="col">
                          {{raiting.Source}}
                        </div>
                        <div class="col">
                          <p class="value">
                            {{raiting.Value}}
                          </p>
                        </div>
                    </div>
                  </div>
              </div>
            </div>
          </div>
      </div>
    </div>

    <div id="result" v-show="isError">
      <p>{{ ErrorInfo }}</p>
    </div>
  </div>
</template>

<script>

class fullFilmInfo {
  constructor(data) {
    this.Title = {
      name: {
        ru: "Название",
        en: "Title"
      },
      // name: "Title",
      value: data.Title
    }
    this.Year = {
      name: {
        ru: "Год производтсва",
        en: "Year"
      },
      value: data.Year
    }
    this.Genre = {
      name: {
        ru: "Жанр",
        en: "Genre"
      },
      value: data.Genre
    }
    this.Released = {
      name: "Released",
      value: data.Released
    }
    this.Runtime = {
      name: {
        ru: "Дата выхода",
        en: "Runtime"
      },
      value: data.Runtime
    }
    this.Director = {
      name: {
        ru: "Режисер",
        en: "Director"
      },
      value: data.Director
    }
    this.Actors = {
      name: {
        ru: "Актеры",
        en: "Actors"
      },
      value: data.Actors
    }
    this.Plot = {
      name: {
        ru: "Описание",
        en: "Plot"
      },
      value: data.Plot
    }
    this.Ratings = {
      name: {
        ru: "Рейтинг",
        en: "Ratings"
      },
      value: data.Ratings
    }
  }
}

export default {
  name: 'OpendbApiList',
  data : function() {
    return  {
      searchTitle : 'Aladin',
      searchYear : '2009',
      baseUrl: `http://www.omdbapi.com/?apikey=4004768b`,
      posterBaseUrl: `http://img.omdbapi.com/?apikey=4004768b`,
      img: 'http://lorempixel.com/350/500/',
      searchUrl:'',
      filmInfo: '',
      ErrorInfo: '',
      isFound: false,
      isError: false,
      isFullInfo: false,
      btnTitle : 'Показать больше'
    }
  },
  methods: {
    findFilms() {
      let that = this
      that.searchUrl = `${that.baseUrl}&t=${that.searchTitle}&y=${that.searchYear}`
      fetch(that.searchUrl).then(function(response) {
        return response.json();
      })
      .then(function(data) {
        if (data.Error) {
          that.isError = true;
          that.isFound = false;
          that.ErrorInfo = data.Error
        } else {
          console.log(data);
          that.isError = false;
          that.isFound = true;
          that.filmInfo = new fullFilmInfo (data);
        }
      });
    },
    showMore () {
      this.isFullInfo = !this.isFullInfo;
      if (this.isFullInfo === true) {
        this.btnTitle = 'Показать меньше'
      } else {
        this.btnTitle = 'Показать больше'
      }
    }
  }
}
</script>

